import React, { Component } from 'react';
import {Container, Row, Table} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import achievements_list from '../data/achievements.json';
import ms from '../image/ms.png';

export default class MyAchievements extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: false,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('myachievements');
      this.element_obj.style.visibility = "hidden";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: false
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY >= this.state.y_position - 100 && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 200) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        ((Math.ceil(window.scrollY) < this.state.y_position - 100) ||
        (Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 200))) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
  
    render() {
      return(
        <Container fluid id="myachievements" style={{padding: 0, minHeight: 600,  overflow: 'hidden'}}>
          <CSSTransition 
            in={this.state.isShow}
            timeout={1000}
            classNames="section"
            onEnter={(el) => {this.element_obj.style.visibility = "visible";}}
            onExited={(el) => {this.element_obj.style.visibility = "hidden";}}
            nodeRef={this.nodeRef}>
            <Container fluid ref={this.nodeRef} style={{padding: '100px 105px 0px 105px', textAlign: 'left', position: 'relative'}}>
              <img src={ms} alt="" style={{width: '100%', position: 'absolute', top: 0, left: 0, zIndex: -1, opacity: 0.5}}></img>
              <Row style={{fontSize: 24, fontWeight: 'bold', marginBottom: 10}}>
                Achievements
              </Row>
              <Row className="justify-content-start"  style={{textAlign: 'justify'}}>
                <Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
                  <thead>
                    <tr>
                      <th style={{width: '10%'}}>Year</th>
                      <th style={{width: '60%'}}>Event</th>
                      <th style={{width: '30%'}}>Organization</th>
                    </tr>
                  </thead>
                  <tbody>
                    {achievements_list.map((achievements_items, index) => {
                      return(
                        <tr>
                          <td>{achievements_items.year}</td>
                          <td>{achievements_items.event}</td>
                          <td>{achievements_items.organization}</td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              </Row>
            </Container>
          </CSSTransition>
        </Container>
      );
    }
}