import React, { Component } from 'react';
import {Container, Navbar, Nav} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import propic from '../image/propic.jpg';
import banner from '../image/banner3.jpeg';

export class Wrap extends Component {
    render() {
      return(
        <Container id="wrap" fluid style={{fontWeight: 330, padding: 0, margin: 0}}>
          {this.props.children}
        </Container>
      );
    }
}
  
export class Title extends Component {
    render() {
        return(
        <Container id="title" style={{padding: 0}}>
            <Navbar expand="lg" variant="light" style={{background: '#e9ecef', height: 250, fontWeight: 200}}>
            <Container style={{padding: 30}}>
                <div style={{width: 650}}>
                <div style={{fontSize: 35, borderBottom: '1px solid grey', marginBottom: 5, lineHeight: 1.5}}>
                    Mr. Chung Ka-ho Ryan <br />
                    鍾嘉浩 <br />
                </div>
                <div style={{fontSize: 18}}>
                    B.Sc. in Computer Science, Year 3 (2020-2021)<br />
                    The Chinese University of Hong Kong
                </div>
                </div>
                <div style={{width: 240, float: 'left'}}>
                <img src={propic} alt="propic" width="100%"></img>
                </div>
            </Container>
            </Navbar>
        </Container>
        );
    }
}
  
export class ContentNav extends Component {
    constructor(props) {
      super(props);
      this.state = {
        floating: false,
        y_position: 0
    };
}
  
    componentDidMount() {
      this.element_obj = document.getElementById('contentnav');
      this.element_space_obj = document.getElementById('contentnav_space');
      this.setState({y_position: Math.round(document.getElementById('contentnav').getBoundingClientRect().top+window.pageYOffset)});
      window.addEventListener('scroll', this.handleScroll);
      window.addEventListener('resize', this.handleResize);
    }
  
    handleScroll = () => {
      if (!this.state.floating && window.scrollY > this.state.y_position) {
        this.element_obj.style.position = 'fixed';
        this.element_obj.style.top = '0px';
        this.element_obj.style.left = document.getElementById("title").getBoundingClientRect().left+window.pageXOffset+'px';
        this.element_space_obj.style.display = "block";
        this.setState({floating: true})
      }
      else if (this.state.floating && window.scrollY <= this.state.y_position) {
        this.element_obj.style.position = 'static';
        this.element_space_obj.style.display = "none";
        this.setState({floating: false})
      }
    }
  
    handleResize = () => {
      this.element_obj.style.left = document.getElementById("title").getBoundingClientRect().left+window.pageXOffset+'px';
    }
  
    ScrollToContent = (div) => {
      var div_y_position = Math.ceil(document.getElementById(div).getBoundingClientRect().top+window.pageYOffset);
      console.log("to " + div + ": " + div_y_position);
      window.scrollTo({top: div_y_position, behavior: 'smooth'});
    }
  
    render() {
      return(
        <div>
          <Container id="contentnav" style={{width: '100%', padding: 0, zIndex: 1}}>
            <Navbar expand="lg" variant="light" style={{height: 40, background: 'rgba(256, 256, 256, 0.9)', padding: '0px 30px', boxShadow: '0 5px 6px -6px rgba(0,0,0,.5)'}}>
              <Navbar.Brand onClick={() => this.ScrollToContent('title')} style={{cursor: 'pointer'}}>My</Navbar.Brand>
              <Nav className="mr-auto">
                <Nav.Link onClick={() => this.ScrollToContent('mybackground')}>Background</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('myprojects')}>Projects</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('myachievements')}>Achievements</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('mycontact')}>Contact</Nav.Link>
              </Nav>
            </Navbar>
          </Container>
          <Container id="contentnav_space" style={{height: 40, width: '100%', display: 'none', padding: 0}}>
          </Container>
        </div>
        );
    }
}
  
export class MyBanner extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: true,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('mybanner');
      this.element_obj.style.visibility = "visible";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: true
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 120) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 120) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
  
    render() {
      return(
        <Container fluid id="mybanner" style={{padding: 0, height: 300, overflow: 'hidden'}}>
          <CSSTransition 
            in={this.state.isShow}
            timeout={1000}
            classNames="section"
            onEnter={(el) => {this.element_obj.style.visibility = "visible";}}
            onExited={(el) => {this.element_obj.style.visibility = "hidden";}}
            nodeRef={this.nodeRef}>
            <Container ref={this.nodeRef} style={{padding: 0}}>
              <img src={banner} alt="network" style={{width: '100%', opacity: 0.9}}></img>
            </Container>
          </CSSTransition>
        </Container>
      );
    }
}
