import React, { Component } from 'react';
import {Container, Button, Row, Table} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import projects_list from '../data/projects.json';
import gear from '../image/gear.jpg';

export default class MyProject extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: false,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('myprojects');
      this.element_obj.style.visibility = "hidden";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: false
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY >= this.state.y_position - 100 && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 200) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        ((Math.ceil(window.scrollY) < this.state.y_position - 100) ||
        (Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 200))) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
  
    render() {
      return(
        <Container fluid id="myprojects" style={{padding: 0, minHeight: 600,  overflow: 'hidden'}}>
          <CSSTransition 
            in={this.state.isShow}
            timeout={1000}
            classNames="section"
            onEnter={(el) => {this.element_obj.style.visibility = "visible";}}
            onExited={(el) => {this.element_obj.style.visibility = "hidden";}}
            nodeRef={this.nodeRef}>
            <Container fluid ref={this.nodeRef} style={{padding: '100px 105px 0px 105px', textAlign: 'left', position: 'relative', background: 'rgba(174, 230, 255, 0.6)'}}>
            <img src={gear} alt="gear" style={{width: '100%', position: 'absolute', top: 0, left: 0, zIndex: -1, opacity: 0.2}}></img>
              <Row style={{fontSize: 24, fontWeight: 'bold', marginBottom: 10}}>
                Project
              </Row>
              <Row className="justify-content-start"  style={{textAlign: 'justify'}}>
                <Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
                  <thead>
                    <tr>
                      <th style={{width: 200}}>Name</th>
                      <th style={{width: 220}}>Platform</th>
                      <th>Description</th>
                      <th>Url</th>
                    </tr>
                  </thead>
                  <tbody>
                    {projects_list.map((projects_items, index) => {
                        return(
                          <tr>
                            <td>{projects_items.name}</td>
                            <td>{projects_items.platform}
                                <p style={{textAlign: 'left', color: 'grey', fontStyle: 'italic'}}>developed using {projects_items.tool}</p>
                            </td>
                            <td>{projects_items.description}</td>
                            <td><Button variant="secondary" size="sm" href={projects_items.url}>{projects_items.repository}</Button></td>
                          </tr>
                        );
                      })}
                  </tbody>
                </Table>
              </Row>
            </Container>
          </CSSTransition>
        </Container>
      );
    }
}