import React, { Component } from 'react';
import {Container, Row} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import dream from '../image/dream.jpg';

export default class MyContact extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: false,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('mycontact');
      this.element_obj.style.visibility = "hidden";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: false
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY >= this.state.y_position - 100 && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 200) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        ((Math.ceil(window.scrollY) < this.state.y_position - 100) ||
        (Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 200))) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
  
    render() {
      return(
        <Container fluid id="mycontact" style={{padding: 0, height: 600,  overflow: 'hidden'}}>
          <CSSTransition 
            in={this.state.isShow}
            timeout={1000}
            classNames="section"
            onEnter={(el) => {this.element_obj.style.visibility = "visible";}}
            onExited={(el) => {this.element_obj.style.visibility = "hidden";}}
            nodeRef={this.nodeRef}>
            <Container ref={this.nodeRef} fluid  style={{padding: '100px 105px 0px 105px', textAlign: 'left', position: 'relative'}}>
            <img src={dream} alt="dream" style={{width: '100%', position: 'absolute', top: 0, left: 0, zIndex: -1, opacity: 0.5}}></img>
              <Row style={{fontSize: 24, fontWeight: 'bold', marginBottom: 10}}>
                Contact
              </Row>
              <Row className="justify-content-start"  style={{width: 600, textAlign: 'justify'}}>
                <Container>
                  <Row>Email:&nbsp;<br /><i><a href="mailto:ckhryan6494@gmail.com" style={{color: 'black'}}>ckhryan6494@gmail.com</a></i></Row>
                  <Row>Linkedin:&nbsp;<br /><i><a href="https://www.linkedin.com/in/ka-ho-ryan-chung-a992671a7/" style={{color: 'black'}}>Ka Ho Ryan Chung</a></i></Row>
                </Container>
              </Row>
            </Container>
          </CSSTransition>
        </Container>
      );
    }
}