import React, { Component } from 'react';
import {Container, Row} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import cs from '../image/cs.jpg';

export default class MyBackground extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: false,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('mybackground');
      this.element_obj.style.visibility = "hidden";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: false
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY >= this.state.y_position - 100 && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 200) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        ((Math.ceil(window.scrollY) < this.state.y_position - 100) ||
        (Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 200))) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
    
    render() {
      return(
        <Container fluid id="mybackground" style={{padding: 0, height: 600,  overflow: 'hidden'}}>
          <CSSTransition 
            in={this.state.isShow}
            timeout={1000}
            classNames="section"
            onEnter={(el) => {this.element_obj.style.visibility = "visible";}}
            onExited={(el) => {this.element_obj.style.visibility = "hidden";}}
            nodeRef={this.nodeRef}>
            <Container fluid ref={this.nodeRef} style={{padding: '210px 105px 0px 105px', textAlign: 'left', position: 'relative'}}>
              <img src={cs} alt="computerscience" style={{width: '100%', position: 'absolute', top: 0, left: 0, zIndex: -1, opacity: 0.1}}></img>
              <Row style={{fontSize: 24, fontWeight: 'bold', marginBottom: 10}}>
                Background
              </Row>
              <Row className="justify-content-start"  style={{width: 600, textAlign: 'justify'}}>
                  I am currently a computer science student studying at CUHK. My enthusiasm in software development has let me keep learning the related knowledge and techniques. Apart from academics, I am also honored to gain some practical experenice in software development from a developer internship offered by a technical company. With my enthusiasm and growing capability, I aspire to be a skilled and professional software developer in my future career. 
              </Row>
            </Container>
          </CSSTransition>
        </Container>
      );
    }
}
  